// respone拦截器
instance.interceptors.response.use(
  response => {
    return response
  },
  error => {
    if (error.response.status === 403) {
      window.location.pathname = '/login'
    }
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)
export default instance
